@import url('https://fonts.googleapis.com/css?family=Josefin+Sans|Major+Mono+Display');

body {
	font-family: medium-content-sans-serif-font, 'Lucida Grande',
		'Lucida Sans Unicode', 'Lucida Sans', Geneva, Arial, sans-serif;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.header {
	color: white;
	padding: 64px 64px 24px 64px;
	display: flex;
	flex-direction: column;
	align-items: center;

	background-color: #bb4e75;
	background-image: linear-gradient(135deg, #ff9d6c -30%, #bb4e75 100%);

	.back {
		display: block;
		color: inherit;
	}

	.bottom {
		display: flex;
		align-items: baseline;
		justify-content: space-between;
		width: 100%;
		margin-top: 24px;
	}

	.title {
		width: 100%;
		margin-top: 24px;
		text-decoration: none;
		color: inherit;
		.a {
			font-family: 'Josefin Sans', sans-serif;
			font-size: 72px;
		}
		.b {
			font-size: 57px;
			font-family: 'Major Mono Display', monospace;
			letter-spacing: -4px;
		}
	}

	.social {
		// move to right of container
		margin-left: auto;
		a {
			display: inline-block;
			margin-right: 24px;
			&:last-of-type {
				margin-right: 0;
			}
			.icon {
				width: 24px;
				path {
					fill: white;
				}
			}
		}
	}

	.avatar {
		width: 100%;
		justify-content: left;
		svg {
			height: 48px;
			margin-right: 12px;
			.st0 {
				fill: #e8e6e7;
			}
			.st1 {
				fill: #fdd7be;
			}
			.st2 {
				stroke: #000000;
				stroke-linecap: round;
				stroke-miterlimit: 10;
			}
			.st3 {
				fill: #cbb29b;
			}
			.st4 {
				fill: #e1bda8;
			}
			.st5 {
				opacity: 0.63;
			}
			.st6 {
				fill: #564134;
			}
			.st7 {
				fill: #dc9195;
			}
			.st8 {
				fill: #c9878d;
			}
			.st9 {
				opacity: 0.63;
				fill: #564134;
			}
			.st10 {
				fill: #434343;
			}
			.st11 {
				opacity: 0.61;
				fill: #e2b473;
			}
			.st12 {
				opacity: 0.34;
			}
			.st13 {
				fill: #e2b473;
			}
			.st14 {
				opacity: 0.62;
				fill: #564134;
			}
			.st15 {
				opacity: 0.83;
				fill: #e1bda8;
			}
			.st16 {
				opacity: 0.83;
				fill: #ccaa9b;
			}
		}
	}

	i {
		margin-right: 10px;
		font-size: 35px;
		text-decoration: none;
		color: white;
	}
}

.posts {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 40px 80px;
}
.post-item {
	color: inherit;
	text-decoration: none;
	width: calc(33.33% - 10px * 2 * 3);
	margin: 10px;
	.image {
		background-size: cover;
		background-position: bottom;
		width: 100%;
		height: 200px;
	}
	.title {
		font-size: 20px;
		font-weight: 600;
		margin: 10px 0;
	}
	.blurb {
		color: #444;
	}
}

.date {
	color: #222;
	font-size: 12px;
	margin: 10px 0;
}

.post {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	.info {
		width: 50%;
		margin: 30px 0;
		h1 {
			font-family: Georgia;
			font-size: 40px;
			margin: 10px 0;
		}
	}
	.image {
		background-size: cover;
		background-position: bottom;
		width: 100%;
		height: 700px;
	}
}

.content {
	width: 60%;
	p {
		margin-top: 29px;
		font-family: medium-content-serif-font, Georgia, Cambria, 'Times New Roman',
			Times, serif;
		font-weight: 400;
		font-style: normal;
		font-size: 21px;
		line-height: 1.58;
		letter-spacing: -0.003em;
	}
	h3 {
		margin-top: 53px;
		font-family: medium-content-sans-serif-font, 'Lucida Grande',
			'Lucida Sans Unicode', 'Lucida Sans', Geneva, Arial, sans-serif;
		font-weight: 600;
		font-style: normal;
		letter-spacing: 0;
		font-size: 30px;
		line-height: 1.15;
		letter-spacing: -0.015em;
	}
	a {
		text-decoration: none;
		background-repeat: repeat-x;
		background-image: linear-gradient(
			to right,
			rgba(0, 0, 0, 0.84) 100%,
			rgba(0, 0, 0, 0) 0
		);
		color: inherit;
		background-size: 1px 1px;
		background-position: 0 1.05em;
		background-position: 0 calc(1em + 1px);
	}
	li {
		font-family: medium-content-serif-font, Georgia, Cambria, 'Times New Roman',
			Times, serif;
		font-weight: 400;
		font-style: normal;
		font-size: 21px;
		line-height: 1.58;
		letter-spacing: -0.003em;
	}
}

// Animations
.post-item {
	.title:after {
		content: '';
		background: linear-gradient(135deg, #ff9d6c -30%, #bb4e75 100%);
		width: 100%;
		height: 4px;
		display: block;
		margin-top: 2px;
		transition: 0.2s transform;
		transition-timing-function: linear;
		transform-origin: top;
		transform: scaleY(0);
	}
	&:hover .title:after {
		transition-timing-function: ease-in;
		transform: scaleY(1);
	}
}

// Responsive

@media only screen and (max-width: 960px) {
	.header {
		padding: 32px 32px 16px 32px;
	}
	.posts {
		padding: 20px;
	}
	.post-item {
		width: calc(50% - 10px * 2 * 2);
	}
	.post {
		.info {
			width: 90%;
			margin: 30px 0;
			h1 {
				font-size: 40px;
				margin: 10px 0;
			}
		}
		.image {
			background-size: cover;
			width: 100%;
			height: 300px;
		}
	}
	.content {
		width: 90%;
	}
}

@media only screen and (max-width: 400px) {
	.header {
		padding: 24px 24px 16px 24px;
	}
	.post-item {
		width: 100%;
	}
}
